@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.LogInForm{max-height:100vh !important;overflow:hidden}.LogInForm .bg-image{background-image:url(/static/media/Login2.46e269a3.png);height:100vh;width:100%;display:inline-block;background-position:center;background-repeat:no-repeat;background-size:cover;position:relative}.LogInForm .logo-container{width:100%;margin:0;padding:20% 0 40px 0}.LogInForm .logo-container .logo{max-width:100%}.LogInForm .container{padding:0 15%;display:grid;grid-template-rows:auto 30px;height:100vh}.LogInForm .container .vertical-gap{margin-bottom:20px}.LogInForm .subtitle{font-size:12px}.LogInForm .subtitle .margin-bottom{margin-bottom:40px}.LogInForm .footer{align-self:center;display:grid;grid-template-columns:1fr 30px;justify-items:end;grid-gap:10px}.LogInForm .btn-grad{background-image:linear-gradient(to right, #64D7EB 0%, #79CBCA 51%, #64D7EB 100%)}.LogInForm .btn-grad{padding:10px 45px;text-align:center;text-transform:uppercase;transition:0.5s;background-size:200% auto;color:white;box-shadow:0 0 20px #eee;border-radius:10px;display:block;float:right}.LogInForm .btn-grad:hover{background-position:right center;color:#fff;text-decoration:none}

#BreadCrumbsContainer{padding:8px 14.3vw}#BreadCrumbsContainer #BreadCrumbs,#BreadCrumbsContainer #divider{max-width:1086px;margin:0 auto}

.nav-container{padding:8px 14.3vw;box-shadow:0px 3px 5px -1px rgba(0,0,0,0.2);-webkit-box-shadow:0px 3px 5px -1px rgba(0,0,0,0.2);-moz-box-shadow:0px 3px 5px -1px rgba(0,0,0,0.2)}.Navbar{max-width:1086px;margin:0 auto;height:67px;display:grid;grid-template-columns:-webkit-max-content 1fr;grid-template-columns:max-content 1fr}.Navbar img{height:50px;width:auto;align-self:center}.Navbar .text{justify-self:end;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;text-align:end;display:grid;grid-template-columns:-webkit-max-content -webkit-max-content -webkit-max-content;grid-template-columns:max-content max-content max-content;align-items:center;grid-gap:10px;font-family:'Montserrat'}.Navbar .text img{border-radius:50%;height:67px;width:67px;float:right}.Navbar .text p{margin:0;padding:0;color:#707070}.Navbar .text .sub{font-size:12px}.Navbar .text .main{font-weight:600;color:#71DAEC;font-size:14px}.Navbar .text .hover-pointer:hover{cursor:pointer}.Navbar .text .dashboard-btn{border-right:1px solid rgba(0,0,0,0.322);padding:10px !important;color:rgba(0,0,0,0.6)}.Navbar .text .dashboard-btn:hover{cursor:pointer}

#footer{display:grid;grid-template-columns:1fr -webkit-max-content;grid-template-columns:1fr max-content;background-color:#efefef;padding:16px;justify-items:end;align-items:center;grid-gap:10px;margin-top:50px}#footer img{height:60px;width:auto;opacity:69%}#footer span{opacity:69%;font-size:14.4px}

